body {
  font-family: "Source Sans Pro", Helvetica, Arial, sans-serif;
  line-height: 1.5;
  font-weight: 300;
}

.module__heading {
  line-height: 1.2;
  margin-bottom: 2.5rem;
}
.module strong {
  font-weight: 600;
}

h1 {
  font-size: 92px;
}

h2 {
  font-size: 72px;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

*:focus:not(:focus-visible) {
  outline: none;
}

*:focus-visible {
  outline: 2px dashed currentColor;
  outline-offset: 2px;
}

body {
  box-sizing: border-box;
  background-color: #f6f6f6;
  color: #3c3c3c;
}

a {
  color: inherit;
}

a:hover,
.hamburger__icon:hover,
.swiper-button-next:hover,
.swiper-button-prev:hover,
.swiper-pagination-bullet:hover {
  opacity: 0.75;
}

a:active,
.hamburger__icon:hover,
.swiper-button-next:active,
.swiper-button-prev:active,
.swiper-pagination-bullet:active {
  opacity: 1;
}

.main {
  overflow-x: hidden;
}
.main .module {
  margin: 10rem auto;
  position: relative;
  overflow: hidden;
}
.main .module__heading {
  margin-bottom: 2.5rem;
}
.main .container {
  max-width: 1140px;
  margin: 40px auto;
}
@media screen and (max-width: 1250px) {
  .main .container {
    padding: 0 5%;
  }
}
.main img {
  width: 100%;
  height: auto;
  display: block;
}
.main ul,
.main li {
  list-style-type: none;
}
.main .text-box p:not(:last-child) {
  margin-bottom: 2rem;
}

/* 404 Template */
.error404 {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  row-gap: unset;
  justify-content: space-between;
}

.error404 .main {
  padding-top: 15vh;
  padding-bottom: 15vh;
  min-height: 50vh;
}

.error404 h1 {
  padding-bottom: 1em;
}

.error404 .main p {
  font-size: 26px;
}

.column-3 {
  display: flex;
}
.column-3-1 {
  width: 33.33%;
}
.column-3-2 {
  width: 66.66%;
  padding-left: 3rem;
}

.column-2 {
  display: flex;
}
.column-2 > * {
  width: 50%;
}

.btn {
  text-decoration: none;
  display: inline-block;
  padding: 0.55em 2.2em;
  width: fit-content;
  margin: 2px;
  cursor: pointer;
}

.hamburger__checkbox {
  display: none;
}
.hamburger__button {
  display: block;
  height: 30px;
  aspect-ratio: 1/1;
  z-index: 99;
  cursor: pointer;
}
.hamburger__icon {
  position: relative;
  display: block;
  top: 50%;
}
.hamburger__icon, .hamburger__icon::before, .hamburger__icon::after {
  width: 30px;
  height: 2px;
  background-color: black;
  transition-duration: 0.5s;
  transition-timing-function: ease-in-out;
  border-radius: 20px;
}
.hamburger__icon {
  transition-duration: 0.1s;
}
.hamburger__icon::before, .hamburger__icon::after {
  content: "";
  position: absolute;
  left: 0;
  transition: all 0.8s;
}
.hamburger__icon::before {
  top: -9px;
}
.hamburger__icon::after {
  top: 9px;
}
.hamburger__checkbox:checked + .hamburger__button .hamburger__icon {
  background-color: transparent;
}
.hamburger__checkbox:checked + .hamburger__button .hamburger__icon::before {
  top: 0;
  transform: rotate(45deg);
}
.hamburger__checkbox:checked + .hamburger__button .hamburger__icon::after {
  top: 0;
  transform: rotate(-45deg);
}

.single-artikel .hamburger__icon, .single-artikel .hamburger__icon::before, .single-artikel .hamburger__icon::after {
  background-color: #fff;
}

.header {
  position: fixed;
  width: 100vw;
  left: 0;
  top: 0;
  background-color: #f6f6f6;
  z-index: 999;
  box-shadow: 0 10px 15px #f6f6f6;
  /*Full Width Menu Open*/
}
.header .container {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  position: relative;
  margin: 2rem auto 1rem auto;
  max-width: 1140px;
}
@media screen and (max-width: 1250px) {
  .header .container {
    padding: 0 5%;
  }
}
.header__logo-box {
  width: 50%;
  max-width: 207px;
  margin-left: 0;
  z-index: 41;
  padding-top: 15px;
}
.header .navigation {
  position: fixed;
  justify-content: flex-end;
  left: 0;
  width: 100vw;
  height: 100vh;
  top: 0;
  z-index: 40;
  background-color: rgba(60, 60, 60, 0.95);
  display: none;
}
.header .navigation .nav_wrapper {
  max-width: 1140px;
  background-color: #fff;
  width: 55%;
}
.header .navigation .header-main-navi {
  max-width: 740px;
  height: 100vh;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  row-gap: unset;
  display: flex;
  align-items: center;
  justify-content: center;
}
.header .navigation .header-main-navi .main-menu {
  display: flex;
  flex-direction: column;
  row-gap: unset;
  justify-content: flex-start;
  list-style-type: none;
  width: 75%;
}
.header .navigation .header-main-navi .main-menu .main-menu-link {
  font-size: 24px;
  line-height: 58px;
  text-decoration: none;
  text-align: left;
  padding: 0;
  display: block;
}
.header .navigation .header-main-navi .main-menu .main-menu-link::before {
  height: 1px;
  bottom: 0;
  top: auto;
}
.header .navigation .header-main-navi .main-menu .menu-item-has-children:last-child {
  position: fixed;
  bottom: 0;
  display: flex;
  flex-direction: column;
  max-width: 900px;
  width: 45%;
}
.header .navigation .header-main-navi .main-menu .menu-item-has-children:last-child .main-menu-link {
  visibility: hidden;
  width: 75%;
}
.header .navigation .header-main-navi .main-menu .menu-item-has-children:last-child .sub-menu .menu-item-depth-1 {
  list-style-type: none;
}
.header .navigation .header-main-navi .main-menu .menu-item-has-children:last-child .sub-menu .menu-item-depth-1::marker {
  color: #fff;
}
.header .navigation .header-main-navi .main-menu .menu-item-has-children:last-child .sub-menu .menu-item-depth-1 .sub-menu-link {
  text-decoration: none;
  font-size: 14px;
  line-height: 24px;
  max-width: 250px;
}
.header .navigation .header-main-navi .main-menu .menu-item-has-children:last-child .sub-menu .linkedin {
  text-align: right;
}
.header .hamburger__checkbox:checked ~ .navigation {
  display: flex;
}
.header .hamburger__checkbox:checked ~ .header__logo-box {
  filter: invert(100);
}

.single-artikel .header {
  background-color: rgb(0, 0, 0);
  box-shadow: 0 10px 15px rgba(0, 0, 0, 0);
}
.single-artikel .header .header__logo-box {
  filter: invert(1);
}

.footer .container {
  padding-top: 5rem;
  padding-bottom: 3rem;
  margin: 0 auto;
  flex-wrap: wrap;
  row-gap: 2rem;
  max-width: 1140px;
}
@media screen and (max-width: 1250px) {
  .footer .container {
    padding-left: 5%;
    padding-right: 5%;
  }
}
.footer .info,
.footer .navigation {
  line-height: 30px;
  width: unset;
}
.footer .info .footer__logo-box,
.footer .navigation .footer__logo-box {
  filter: invert(1);
  padding-bottom: 30px;
}
.footer .info__heading,
.footer .navigation__heading {
  font-weight: 500;
  margin-bottom: 1em;
}
.footer .info__content .main-menu,
.footer .navigation__content .main-menu {
  list-style-type: none;
}
.footer .info__content .main-menu a,
.footer .navigation__content .main-menu a {
  text-decoration: none;
}
.footer .info {
  min-width: 300px;
  width: 50%;
  max-width: 100vw;
}
.footer .left {
  margin-right: 10%;
}

.hero .container {
  display: flex;
  flex-direction: row;
  column-gap: unset;
  justify-content: space-between;
}
.hero .container .content-wrapper {
  display: flex;
  flex-direction: column;
  row-gap: unset;
  width: 55%;
}
.hero .container .image-wrapper {
  width: 380px;
  height: fit-content;
}
@media screen and (max-width: 1080px) {
  .hero .container .image-wrapper {
    right: -8vw;
  }
}
@media screen and (max-width: 900px) {
  .hero .container {
    display: flex;
    flex-direction: column;
    row-gap: 2rem;
  }
  .hero .container .image-wrapper {
    order: 1;
    max-width: 380px;
    width: 60%;
    margin-left: auto;
  }
}
@media screen and (max-width: 900px) and (max-width: 680px) {
  .hero .container .image-wrapper {
    width: 250px;
    height: fit-content;
    max-width: 250px;
  }
}
@media screen and (max-width: 900px) {
  .hero .container .image-wrapper img {
    transform: translateX(10%);
  }
  .hero .container .content-wrapper {
    order: 2;
    width: 100%;
  }
}

.project-slider .container {
  max-width: 1600px;
  width: 100%;
  overflow: hidden;
}
.project-slider .module__heading {
  text-align: center;
}
.project-slider .slide {
  width: 560px;
  aspect-ratio: 1.3/1;
  border-radius: 15px;
  background-color: #191919;
  overflow: hidden;
  position: relative;
  padding-left: 27px;
  padding-top: 36px;
  color: white;
  font-family: "Barlow Semi Condensed";
}
@media screen and (max-width: 900px) {
  .project-slider .slide {
    width: unset;
  }
}
.project-slider .slide__title {
  font-weight: 600;
}
.project-slider .slide__image {
  position: absolute;
  width: 100%;
  top: 50%;
  left: 8%;
  transform: rotate(-10deg);
}
.project-slider .slide__image img {
  border-radius: 0;
}
.project-slider .slide.active .slide__image {
  position: absolute;
}
.project-slider .swiper-slide-active .slide {
  box-shadow: 0 0 10px 5px rgb(184, 183, 183);
}
.project-slider .swiper-slide-active .slide .slide__image {
  position: absolute;
}
.project-slider .swiper-slide {
  width: fit-content;
  margin-top: 20px;
  --add-bottom: 50px;
  padding-bottom: var(--add-bottom);
}
.project-slider .swiper-slide [class^=swiper-button-] {
  top: calc(50% - var(--add-bottom) / 2);
}
.project-slider .swiper-pagination {
  border-radius: 100%;
  --swiper-pagination-color: #fff;
  --swiper-pagination-bullet-horizontal-gap: 5px;
}
.project-slider .swiper-pagination-bullet {
  color: #3c3c3c;
  opacity: 1;
}
.project-slider .swiper-button-next,
.project-slider .swiper-button-prev {
  --swiper-navigation-size: 24px;
  bottom: 5px;
  top: unset;
  color: #3c3c3c;
  z-index: 11;
}
.project-slider .swiper-button-next {
  right: calc(50% - 150px);
}
@media screen and (max-width: 900px) {
  .project-slider .swiper-button-next {
    overflow: visible;
  }
}
.project-slider .swiper-button-prev {
  left: calc(50% - 150px);
}

.one-column .container {
  max-width: 1140px;
}
.one-column .container .text-box {
  max-width: 560px;
}
.one-column .container .text-box.zentriert {
  margin-left: auto;
  margin-right: auto;
}

.image-gallery {
  padding-bottom: 160px;
}
.image-gallery .container {
  display: flex;
  flex-direction: row;
  column-gap: unset;
  justify-content: flex-start;
  flex-wrap: wrap;
  row-gap: 25px;
  column-gap: 25px;
  position: relative;
}
.image-gallery .container .caption {
  display: none;
}
.image-gallery .container .close {
  display: none;
}
.image-gallery .container .single-img {
  width: 363px;
  height: 242px;
  object-fit: none;
}
.image-gallery .popped {
  overflow-y: hidden;
}
.image-gallery .popped .header {
  display: none;
}
.image-gallery .popped .lightbox {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.9);
  z-index: 9;
}
.image-gallery .popped .lightbox .caption {
  display: flex;
  color: #fff;
  width: 80vw;
  max-width: 560px;
  margin-left: 5vw;
  font-size: 21px;
}
.image-gallery .popped .lightbox .close {
  display: flex;
  color: #FF9B00;
  font-size: 30px;
  top: 30px;
  right: 30px;
  position: fixed;
}
.image-gallery .popped .lightbox .single-img {
  width: 80vw;
  min-width: 320px;
  max-width: 1920px;
  height: auto;
  object-fit: none;
}
@media screen and (max-width: 900px) {
  .image-gallery .popped .container {
    display: flex;
    flex-direction: column;
    row-gap: 2rem;
  }
  .image-gallery .popped .container .image-wrapper {
    order: 1;
    max-width: 380px;
    width: 60vw;
    margin-left: auto;
  }
  .image-gallery .popped .container .image-wrapper img {
    transform: translateX(10%);
  }
}